import React from "react";
import Language from "./languages";
import react from "../assets/LanguageLogos/react.svg";
import angular from "../assets/LanguageLogos/angular.svg";
import python from "../assets/LanguageLogos/python.svg";
import java from "../assets/LanguageLogos/java.svg";
import node from "../assets/LanguageLogos/node.svg";
import c from "../assets/LanguageLogos/c.svg";
import html from "../assets/LanguageLogos/html.svg";
import css from "../assets/LanguageLogos/css.svg";
import js from "../assets/LanguageLogos/javascript.svg";

const LanguageSection = () => {
  return (
    <div className="w-full flex justify-center items-center">
      <div className="w-2/3 flex flex-col flex-wrap gap-10 items-center">
        <p className="text-3xl">General Programming</p>
        <div className="flex flex-row flex-wrap gap-5 justify-evenly">
          <Language
            value={70}
            language="Python"
            logo={python}
            color="#387eb8"
          />
          <Language value={70} language="Java" logo={java} color="#E76F00" />
          <Language value={60} language="C" logo={c} color="#659ad2" />
        </div>
        <p className="text-3xl">Web</p>
        <div className="flex flex-row flex-wrap gap-5 justify-evenly">
          <Language value={75} language="HTML" logo={html} color="#e44d26" />
          <Language value={65} language="CSS" logo={css} color="#2965f1" />
          <Language
            value={70}
            language="JavaScript"
            logo={js}
            color="#f7df1e"
          />
          <Language value={60} language="Node" logo={node} color="#619857" />
        </div>
        <p className="text-3xl">Frameworks</p>
        <div className="flex flex-row flex-wrap gap-5 justify-evenly">
          <Language value={60} language="React" logo={react} color="#61DAFB" />
          <Language
            value={50}
            language="Angular"
            logo={angular}
            color="#DD0031"
          />
        </div>
      </div>
    </div>
  );
};

export default LanguageSection;
